.SearchBar {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 1rem;
    margin-bottom: 6.33rem;
  }
  
  .SearchBar input {
    width: 287px;
    padding: .88rem 0;
    border: 1px solid #fff;
    border-radius: 3px;
    margin-bottom: 2.22rem;
    color: #010c3f;
    text-align: center;
    font-size: 1rem;
    font-family: 'Happy Monkey';
    font-weight: bold;
  }
  
  .SearchBar input:focus {
    outline: none;
  }
  
  .SearchBar a {
    cursor: pointer;
    width: 8.11rem;
    padding: .77rem 0;
    border-radius: 54px;
    background-color: #0da39c;
    text-align: center;
    font-size: .833rem;
    transition: background-color .25s;
    font-weight: bold;
  }
  
  .SearchBar a:hover {
    background-color: rgba(148, 10, 175, 0.7);
  }