.SearchResults {
    width: 45%;
    height: 950px;
    overflow-y: scroll;
    padding: .88rem;
    background-color: rgba(148, 10, 175, 0.7);
    box-shadow: 0 4px 4px 0px #000000;
    font-weight: normal;
    
  }

  .SearchResults h2{
    font-weight: normal;
    border-bottom: 1px solid #6f6f6f;
  }
  
  
  ::-webkit-scrollbar {
      width: 0px;
      background: transparent;
  }
  
  @media only screen and (max-width: 1020px) {
    .SearchResults {
      width: 90%;
      margin-bottom: 2rem;
    }
  }